











































import Vue from 'vue';

export default Vue.extend({
  props: {
    options: {
      type: Object,
      default: () => ({
        duration: 1500,
        perspective: 1500,
        delay: 1000,
        autoplay: false,
        loop: false,
      }),
    },

    list: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      active: 0,
      direction: 0,
      zIndex: [],
      skip: false,
      interval: 0,
    };
  },

  watch: {
    'options.autoplay': {
      handler(val) {
        if (val) {
          this.interval = setInterval(() => {
            const i = this.active + this.direction;

            if (
              this.options.loop &&
              i === this.list.length - 1 &&
              this.direction < 0
            ) {
              this.goToStart();
              return;
            }

            if (!this.skip) {
              this.onCardClick(i, false);
            }

            this.skip = false;
          }, this.options.duration + this.options.delay);
        } else {
          clearInterval(this.interval);
        }
      },
      immediate: true,
    },
  },

  methods: {
    isFlipped(i: number) {
      return i < this.active;
    },

    getStyle(i: number) {
      return {
        zIndex: this.zIndex[i] || -i,
        transitionDuration: `${this.options.duration}ms`,
      };
    },

    calcDirection() {
      if (this.active === 0) {
        if (this.direction < 0) {
          this.direction = 0;
        } else {
          this.direction = 1;
        }
      } else if (this.active === this.list.length) {
        if (this.direction > 0) {
          this.direction = 0;
        } else {
          this.direction = -1;
        }
      }
    },

    goToStart() {
      const l = this.list.length - 1;
      const getT = (i: number) => (this.options.duration / 6) * (l + 1 - i);

      for (let i = l; i >= 0; i--) {
        setTimeout(() => {
          this.onCardClick(i, false);
        }, getT(i));
      }
    },

    onCardClick(i: number, s: boolean) {
      this.$set(this.zIndex, i, i);

      if (this.isFlipped(i)) {
        this.active -= 1;

        setTimeout(() => {
          this.$set(this.zIndex, i, this.zIndex[i] * -1);
        }, this.options.duration / 2);
      } else {
        this.active += 1;
      }

      this.skip = s;
      this.calcDirection();
    },
  },
});
